import { createContext } from 'react';

export interface AppState {
  situacao: object;
  selecionado: string;
  destinos: string[];
  showInput: boolean;
  paginaAtual: number;
  totalPaginas: number;
  carregando: boolean;
  showGotoPageInput: boolean;
  separador: string;
  botaoClickAtivo: string;
  botoesClickClicados: string[];
  loadPDF: Function;
  focaNaDivPrincipal: Function;
  setState: Function;
  processosList: string[];
}

export const defaultState: AppState = {
  showInput: false,
  situacao: {},
  selecionado: '0',
  destinos: [
    'REVISAO',
    'AGUARDA INSCRIO',
    'REQUERIMENTOS',
    'INSCRIO-AJUIZAMENTO',
  ],
  paginaAtual: 0,
  totalPaginas: 0,
  carregando: false,
  showGotoPageInput: false,
  separador: ',',
  botaoClickAtivo: '',
  botoesClickClicados: [],
  processosList: [],
  loadPDF: () => ({}),
  focaNaDivPrincipal: () => ({}),
  setState: () => ({}),
};

const Context = createContext(defaultState);

export default Context;


